/* Contents */
body {
  width: 100vw;
  overflow-y: auto;
  overflow-x: hidden; }
.container-fluid {
  position: relative;
  display: flex;
  padding-left: 0;
  padding-right: 0;
  width: 100vw;
  overflow-x: hidden;
  overflow-y: auto; }
.side-cover {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: -1px;
  left: 0;
  transition: all .8s ease-in-out;
  border-right: 1px solid #000;
  z-index: 3;
  .cover-img {
    width: 100%;
    height: 100%;
    position: relative;
    &:after {
      content: "";
      width: 100%;
      height: 100%;
      background: #000;
      opacity: .3;
      position: absolute;
      top: 0;
      left: 0; }
    img {
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      object-position: center;
      filter: grayscale(80%); } }
  .text {
    position: absolute;
    left: 3rem;
    bottom: 3rem;
    z-index: 5;
    color: #FFF;
    h1 {
      font-weight: 700;
      margin-bottom: 0;
      opacity: 0;
      transform: translateX(-20px);
      transition: all .3s ease-in-out;
      transition-delay: .9s; }
    h2.font-signature {
      font-size: 5rem;
      line-height: .8em;
      margin-bottom: 0;
      font-weight: 500;
      letter-spacing: 2px;
      opacity: 0;
      transform: translateX(20px);
      transition: all .3s ease-in-out;
      transition-delay: .9s;
      // -webkit-text-fill-color: #FFF
      // -webkit-text-stroke-width: 1px
 } } }      // -webkit-text-stroke-color: red

.side-content {
  margin-left: 70vw;
  width: 30vw;
  position: relative;
  opacity: 0;
  transition: opacity .8s ease-in-out;
  transition-delay: .9s;
  .content {
    min-height: 100vh;
    .masthead {
      position: relative;
      text-align: center;
      color: #FFF;
      &:after {
        content: "";
        width: 100%;
        height: 100%;
        background: #000;
        opacity: .4;
        position: absolute;
        top: 0;
        left: 0; }
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center; }
      .countdown {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 2rem;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        z-index: 3; }
      .timer {
        margin-bottom: 2rem;
        figcaption {
          color: #FFF; }
        .timeTo {
          height: max-content; }
        .timeTo.timeTo-black div {
          background: transparent; } }
      .date {
        font-size: 32px;
        line-height: 1.5em; } } }

  .detail {
    padding: 2rem;
    text-align: center;
    h3 {
      font-size: 2rem;
      line-height: 1.5em;
      font-weight: 500;
      small {
        display: block;
        font-weight: 400;
        font-size: 1.5em;
        line-height: 1; } }
    .title {
      h2 {
        font-size: 4rem;
        line-height: 1;
        position: relative;
        &:before,
        &:after {
          content: "";
          width: 3px;
          height: 8vh;
          background: #212529;
          display: block;
          margin: 0 auto 1rem; }
        &:before {
          margin-bottom: 2rem; }
        &:after {
          margin-top: 2rem; } } }
    .activities {
      margin-bottom: 2rem;
      .font-signature {
        margin-bottom: 1.5rem; }
      h3 {
        margin-bottom: 1rem; }
      p,
      address {
        font-size: 1.5rem;
        line-height: 1em; } }
    .btn-border {
      display: block;
      max-width: max-content;
      margin: 0 auto;
      padding: 0.75rem 1.5rem;
      border: 1px solid #212529;
      border-radius: 8px;
      color: #212529;
      text-decoration: none;
      transition: all .3s ease-in-out;
      &:hover {
        background: #212529;
        color: #FFF; } } } }

// Ready
.ready {
  .side-cover {
    width: 70vw;
    .text {
      h1,
      h2.font-signature {
        opacity: 1;
        transform: translateX(0); } } }
  .side-content {
    opacity: 1; } }

// Mobile
@include mobile() {
  .container-fluid {
    display: block; }
  .side-cover {
    position: relative;
    .text {
      left: 1.5rem;
      right: 1.5rem;
      margin: 0 auto;
      h1 {
        font-size: 36px;
        line-height: 1.5em;
        text-align: center; }
      h2.font-signature {
        text-align: center;
        font-size: 5rem;
        span {
          display: block; } } } }
  .side-content {
    width: 100vw;
    margin-left: 0; }


  .ready {
    .side-cover {
      width: 100vw; } } }
